
import noData from '@/components/noData'
import back from '@/components/back'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VAxis from 'vux/src/components/v-chart/v-axis.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VGuide from 'vux/src/components/v-chart/v-guide.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
components: {
noData,
back,
VChart,
VLine,
VAxis,
VScale,
VGuide,
VPoint,
VTooltip
},
data() {
return {
typeH: parseInt(this.$route.query.type),
item: parseInt(this.$route.query.item) || -7,
chartData:[],
chartDataPulse:[],
colors:["#3069cf","#30cf77","#ffa417"],
type:"text",
tagH: {
position: [],
content: '',
direct: 'tl',
offsetY: -10,
background: {
fill: '#3069cf'
},
pointStyle: {
fill: '#3069cf'
},
},
tagL: {
position: [],
content: '',
direct: 'tl',
offsetY: -10,
background: {
fill: '#30cf77'
},
pointStyle: {
fill: '#30cf77'
},
},
tagP:{
position: [],
content: '',
direct: 'tl',
offsetY: -10,
background: {
fill: '#ffa417'
},
pointStyle: {
fill: '#ffa417'
},
},
sugarType:["餐前","餐后","随机"],
page:1,
idx:0,
}
},
created() {
switch(parseInt(this.$route.query.type)){
case 1:
this.getPressure();
break;
case 2:
this.getSugar();
break;
case 3:
this.getFat();
break;
}
},
mounted() {
switch(parseInt(this.$route.query.type)){
case 1:
document.title = "血压"
break;
case 2:
document.title = "血糖"
break;
case 3:
document.title = "体脂"
break;
}
},
beforeDestroy() {

},
methods: {
getDate(day){

let time = new Date();
time.setDate(time.getDate() + day);
let y = time.getFullYear();
let m = time.getMonth() + 1;
let d = time.getDate();
return y + "-" + m + "-" + d;
},
getCurrentTime(){

let time = new Date();
let h = time.getHours();
let min = time.getMinutes();
let s = time.getSeconds();
return h + ":" + min + ":" + s;
},
getPressure(){
this.$post('/MonitorAPI/Monitoring/GetJC_BloodPressureList',{
PatientId: this.$route.query.PatientId,
StartTime:this.getDate(this.item) + " 00:00:00",
EndTime:this.getDate(0) + " " + this.getCurrentTime(),
PageIndex:this.page,
PageSize:500,
}).then(res => {


if(res.ReturnData && res.ReturnData.length != 0){
if(res.ReturnData.length < 2){
this.type = "tag"
}
this.list = res.ReturnData;
res.ReturnData.forEach((v,idx) => {
let _date = v.MeasureTime.split(" ")[0];
_date = _date.split("-")[1] + "/" + _date.split("-")[2]
if(idx == 0){
this.tagH.position = [_date,v.HighPressure];
this.tagH.content = v.HighPressure;
this.tagL.position = [_date,v.LowVoltage];
this.tagL.content = v.LowVoltage;
this.tagP.position = [_date,v.Pulse];
this.tagP.content = v.Pulse
}
this.chartData.unshift({
date:_date,
type:"收缩压",
value:v.HighPressure
},{
date:_date,
type:"舒张压",
value:v.LowVoltage
});
this.chartDataPulse.unshift({
date:_date,
type:"脉率",
value:v.Pulse
})
})
this.idx ++;
}else{
this.idx ++;
this.chartData = []
}

})
},
getSugar(){
this.$post('/MonitorAPI/Monitoring/GetJC_BloodSugarDataList',{
PatientId: this.$route.query.PatientId,
StartTime:this.getDate(this.item) + " 00:00:00",
EndTime:this.getDate(0) + " " + this.getCurrentTime(),
PageIndex:this.page,
PageSize:500,
}).then(res => {

if(res.ReturnData && res.ReturnData.length != 0){
if(res.ReturnData.length < 2){
this.type = "tag"
}
this.list = res.ReturnData;
res.ReturnData.forEach((v,idx) => {
let _date = v.MeasureTime.split(" ")[0];
_date = _date.split("-")[1] + "/" + _date.split("-")[2]
if(this.tagH.position.length == 0 && v.SugarType == 0){
this.tagH.position = [_date,v.SugarValue];
this.tagH.content = v.SugarValue;
}else if(this.tagL.position.length == 0 && v.SugarType == 1){
this.tagL.position = [_date,v.SugarValue];
this.tagL.content = v.SugarValue;
}else if(this.tagP.position.length == 0  && v.SugarType == 2){
this.tagP.position = [_date,v.SugarValue];
this.tagP.content = v.SugarValue;
}else{

}
this.chartData.unshift({
date:_date,
type:this.sugarType[v.SugarType],
value:v.SugarValue
});
})
this.idx ++;
}else{
this.idx ++;
this.chartData = []
}
})
},
getFat(){
this.$post('/MonitorAPI/Monitoring/GetJC_BodyFatDataList',{
PatientId: this.$route.query.PatientId,
StartTime:this.getDate(this.item) + " 00:00:00",
EndTime:this.getDate(0) + " " + this.getCurrentTime(),
PageIndex:this.page,
PageSize:500,
}).then(res => {
if(res.ReturnData && res.ReturnData.length != 0){
if(res.ReturnData.length < 2){
this.type = "tag"
}
this.list = res.ReturnData;
res.ReturnData.forEach((v,idx) => {
let _date = v.MeasureTime.split(" ")[0];
_date = _date.split("-")[1] + "/" + _date.split("-")[2]
if(idx == 0){
this.tagH.position = [_date,v.Height];
this.tagH.content = v.Height;
this.tagL.position = [_date,v.Weight];
this.tagL.content = v.Weight;
this.tagP.position = [_date,v.BFR];
this.tagP.content = v.BFR + "%"
}
this.chartData.unshift({
date:_date,
type:"身高",
value:v.Height
},{
date:_date,
type:"体重",
value:v.Weight
},{
date:_date,
type:"体脂率",
value:v.BFR
});
})
this.idx ++;
}else{
this.idx ++;
this.chartData = []
}
})
},
detailsData(){

if(!this.chartData || this.chartData.length == 0){
AlertModule.show({
content: "暂无数据",
})
}else{
this.$router.push({
path: '/healthItemD',
query: {
PatientId: this.$route.query.PatientId,
item:this.item,
type:this.$route.query.type
}
});
}

}
}
}



